<template>
    <div class="page-wrapper">
        <div class="filter-wrapper">
            <div class="search-wrapper">
                <el-input placeholder="" v-model="searchKey" class="input-with-select">
                    <i slot="prepend" class="el-icon-search"></i>
                    <el-button slot="append" @click="getVideoList"></el-button>
                </el-input>
            </div>
            <div class="video-type">
                <el-select v-model="selectedVideoType" clearable placeholder="" @change="getVideoList">
                    <el-option
                            v-for="item in videoTypes"
                            :key="item.cateId"
                            :label="item.cateName"
                            :value="item.cateId"
                            >
                    </el-option>
                </el-select>
            </div>
            <div class="video-sort sort-by-heat">
                <span class="caret-wrapper">
                    <i class="sort-caret ascending" :class="heatSelected === 'ASC' ? 'ASC':''" @click="toggleHeat('ascending')"></i>
                    <i class="sort-caret descending" :class="heatSelected === 'DESC' ? 'DESC':''"  @click="toggleHeat('descending')"></i>
                </span>
            </div>
            <div class="video-sort sort-by-heat">
                <span class="caret-wrapper">
                    <i class="sort-caret ascending" :class="uploadTimeSelected === 'ASC' ? 'ASC':''" @click="toggleTime('ascending')"></i>
                    <i class="sort-caret descending" :class="uploadTimeSelected === 'DESC' ? 'DESC':''" @click="toggleTime('descending')"></i>
                </span>
            </div>

        </div>
        <div class="video-list-wrapper" v-loading="loading">
            <div class="video-list" v-for="video in videoListModel" @click="playVideo(video)">
                <el-card class="video-card" >
                    <div class="cover">
                        <img :src="video.coverUrl"/>
                    </div>
                    <div class="title">
                       <span>{{video.title}}</span>
                    </div>
                    <div class="abstract">
                        <span>{{video.abstract}}</span>
                    </div>
                    <el-divider></el-divider>
                    <div class="info">
                        <div class="duration">
                            <!--{{formateTime(video.duration)}}-->
                            <i class="el-icon-time"/>
                            <span >{{transTime(video)}}</span>
                        </div>
                        <div class="heat">
                            <el-rate
                                    v-model="video.heat"
                                    disabled
                                    show-score
                                    :colors=heatColors
                                    >
                            </el-rate>
                        </div>
                    </div>
                </el-card>
            </div>
        </div>
        <div class="pagination">
            <el-pagination
                    background
                    :page-size="pageSize"
                    :current-page.sync="currentPage"
                    layout="prev, pager, next"
                    :total="totalVideos"
                    @current-change="handleCurrentChange">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import untils from '../../../untils/index'
    export default {
        name: "home",
        data() {
            return {
                searchKey: '',
                videoListModel: [
                ],
                loading:false,
                heatColors:["#F64A4A","#F64A4A","#F64A4A"],
                pageSize:8,
                totalVideos:0,
                currentPage:1,
                videoTypes:[],
                selectedVideoType:'',
                heatSelected:'',
                uploadTimeSelected:''
            }
        },
        created() {
            this.getVideoTypes();
            this.getVideoList();

        },
        methods: {
            playVideo(video) {
                this.$store.commit('setVideoInfo',video);
                this.$router.push({
                    path:'/main/videoPlaying',
                    query:{
                        id:video.id
                    }
                })
            },
            getVideoList() {
                this.loading = true;
                let param = {
                    queryConds: [],
                    sortConds: [
                        {
                            sortFieldName: "heat",
                            sortRule: "ASC"
                        }
                    ],
                    videoKind:'pub',
                };
                param.pageNum=this.currentPage;
                param.perPageRecords =  this.pageSize;
                param.queryConds.push({
                    key: "filterStr",
                    value: this.searchKey
                });
                this.$http.post('/videoinfo/filters',param).then(res => {
                    if(res.data.code === 'success') {
                        this.videoListModel = res.data.data.videoInfos;
                        this.totalVideos = res.data.data.totalVideoNums;
                        this.loading = false;
                    }else {
                        this.$notify({
                            title:'',
                            type:'error',
                            message:res.data.msg
                        });
                        this.loading = false
                    }
                }).catch((err)=>{
                    this.loading = false
                    this.$notify({
                        title:'',
                        type:'error',
                        message:err.message
                    });
                })
            },
            handleCurrentChange() {
                this.getVideoList()
            },
            transTime(video) {
                return  untils.formatTime(video.duration)
            },
            getVideoTypes() {
                this.$http.get('/videoTypes').then(res=>{
                    if(res.data.code ==='success') {
                        this.videoTypes = res.data.data;
                    }
                })
            },
            toggleHeat(value) {
                if(value === 'descending') {
                    if(this.heatSelected === 'DESC') {
                        this.heatSelected = '';
                    } else{
                        this.heatSelected = 'DESC'
                    }
                }else {
                    if(this.heatSelected === 'ASC') {
                        this.heatSelected = '';
                    } else{
                        this.heatSelected = 'ASC'
                    }
                }
            },
            toggleTime(value) {
                if(value === 'descending') {
                    if(this.uploadTimeSelected === 'DESC') {
                        this.uploadTimeSelected = '';
                    } else{
                        this.uploadTimeSelected = 'DESC'
                    }
                }else {
                    if(this.uploadTimeSelected === 'ASC') {
                        this.uploadTimeSelected = '';
                    } else{
                        this.uploadTimeSelected = 'ASC'
                    }
                }
            },
        }
    }
</script>

<style scoped lang="scss">
    .filter-wrapper {
        height: 100px;
        background-color: #fff;
        display: flex;
        padding: 30px 25px;
        box-sizing: border-box;
    }
    .search-wrapper {
        height: 48px;
        width: 350px;
        margin: 0px 20px;
    }
    .video-list-wrapper {
        display: flex;
        flex-wrap: wrap;
    }
    .video-list {
        margin: 30px;
    }
    .cover {
        width: 250px;
        height: 160px;
    }
    .cover  img {
        height: 100%;
        width: 100%;
    }
    .info {
        display: flex;
        justify-content: space-between;

    }
    .info .duration i{
       margin-right: 5px;
    }
    .title {
        width: 250px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
    .pagination {
        position: fixed;
        top:35%;
        right: 0px;
    }
    .video-sort {
        line-height: 40px;
        margin: 0px 20px;
         .caret-wrapper {
            display: inline-flex;
            flex-direction: column;
            align-items: center;
            height: 34px;
            width: 24px;
            vertical-align: middle;
            cursor: pointer;
            overflow: initial;
            position: relative;
             .sort-caret {
                 width: 0;
                 height: 0;
                 border: 5px solid transparent;
                 position: absolute;
                 left: 7px;
                 &.ascending {
                     border-bottom-color: #c0c4cc;
                     top: 5px;
                     &.ASC{
                         border-bottom-color: #e70012;
                     }
                 }
                 &.descending {
                     border-top-color: #c0c4cc;
                     bottom: 7px;
                     &.DESC{
                         border-top-color: #e70012;
                     }
                 }
             }
        }
    }
</style>