<template>
    <div style="height: 100%;">
        <div class="play-header">
            <div class="title">
                <img src="../../../assets/image/playing.png">
                <span class="text-in-red">:</span>
                <span>{{videoInfo.title}}</span>
            </div>
            <div class="operation">
                <div class="mark" @click="handleMark">
                    <img src="../../../assets/image/marked.png">
                </div>
                <div class="go-back" @click="goBckToVideoList">
                    <img src="../../../assets/image/goBack.png">
                </div>
            </div>
        </div>
        <div class="play-content">
            <div class="player-wrapper">
                <div id="player"></div>
            </div>
            <div class="info-note">
                <el-tabs v-model="activeTab" type="border-card" @tab-click="handleTabsChange">
                    <el-tab-pane label="" name="info">
                        <div>
                            <h4>{{videoInfo.title}}</h4>
                        </div>
                        <div style="width: 100%">
                            <img style="width: 100%" :src="videoInfo.coverUrl"/>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="" name="note">
                        <el-input
                                type="textarea"
                                :rows="2"
                                placeholder=""
                                v-model="note">
                        </el-input>
                        <div class="add-note">
                            <el-button type="primary" icon="el-icon-edit-outline" @click="addNote" :loading="addingNote"></el-button>
                        </div>
                        <div class="note-timeline">
                            <el-timeline >
                                <el-timeline-item v-for="nodeValue in notes" :timestamp="formateTime(nodeValue.time)" placement="top" :key="nodeValue.time">
                                    <el-card>
                                        <p>{{nodeValue.info}}</p>
                                    </el-card>
                                </el-timeline-item>
                            </el-timeline>
                        </div>
                    </el-tab-pane>
                </el-tabs>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import untils from '../../../untils/index'
    export default {
        name: "video-editor",
        data() {
            return {
                activeTab:'note',
                notes:'',
                note:'',
                currentPlayingTime:0,
                player:null,
                isMarked:false,
                addingNote:false,
                reverse:true
            }
        },
        created(){
            // this.$route.query.id
            if(!this.videoInfo.id) {
               this.goBckToVideoList()
            }else {
                this.getNoteById();
                this.getMarkInfo();
            }
        },
        computed:{
            ...mapState(['videoInfo'])
        },
        mounted() {
            let playerProps= {
                id:'player',
                width:'100%',
                height:'485px',
                autoplay:true,
                controlBarVisibility:'always',
                rePlay:true,
                source:this.videoInfo.url,
                cover:this.videoInfo.CoverURL
            };
            if(!this.player) {
                this.player = new  Aliplayer(playerProps);
                this.player.on('timeupdate',(value) => {
                    this.currentPlayingTime=this.player.getCurrentTime();
                })
            }
        },
        methods: {
            handleTabsChange(tab) {

            },
            goBckToVideoList() {
                this.$router.push('videoList')
            },
            getNoteById() {
                let url = '/studyrecords?videoId='+this.videoInfo.videoId;
                this.$http.get(url).then( res => {
                    if(res.data.code === 'success') {
                        this.notes = res.data.data.sort(function (item1, item2) {
                            return  parseInt(item2.time) - parseInt(item1.time)
                        });
                        console.log(this.note)
                    }else {
                        this.$notify({
                            title:'',
                            type:'error',
                            message:res.data.msg
                        });
                    }
                }).catch( err => {
                    this.$notify({
                        title:'',
                        type:'error',
                        message:err.message
                    });
                })
            },
            formateTime(timeStr) {
                return untils.formatTime(timeStr) ;
            },
            addNote() {
                this.addingNote = true;
                let param = {
                    id:this.videoInfo.id,
                    info:this.note,
                    time:this.currentPlayingTime,
                    userName:localStorage.getItem('user'),
                    videoId:this.videoInfo.videoId
                }
                this.$http.post('/studyrecords',param).then(res => {
                    if(res.data.code === 'success') {
                        this.note = ''
                        this.getNoteById()
                        this.addingNote = false;
                    }else {
                        this.$notify({
                            title:'',
                            type:'error',
                            message:res.data.msg
                        });
                        this.addingNote = false;
                    }
                }).catch(err=>{
                    this.$notify({
                        title:'',
                        type:'error',
                        message:err.message
                    });
                    this.addingNote = false;
                })
            },
            handleMark() {
                if(this.isMarked){
                    this.unMark()
                }else {
                    this.mark()
                }
            },
            mark(){
                let param = {
                    userName:localStorage.getItem('user'),
                    videoId: this.videoInfo.videoId,
                    id: this.videoInfo.id,
                    createTime:this.videoInfo.creatTime
                };
                this.$http.post('/favoriteInfos',param).then(res => {
                    if(res.data.code === 'success') {
                        this.isMarked = true;
                        this.$notify({
                            title:'',
                            type:'success',
                        });
                    }else {
                        this.$notify({
                            title:'',
                            type:'error',
                            message:res.data.msg
                        });
                    }
                })
            },
            unMark() {
                let url = '/favoriteInfos/'+this.videoInfo.videoId;
                this.$http.delete(url).then( res=>{
                    if(res.data.code === 'success') {
                        this.isMarked = false;
                        this.$notify({
                            title:'',
                            type:'success',
                        });
                    }else {
                        this.$notify({
                            title:'',
                            type:'error',
                            message:res.data.msg
                        });
                    }
                })
            },
            getMarkInfo() {
                let url =`/isfavorite?videoId=${this.videoInfo.videoId}`
                this.$http.get(url).then( res =>{
                    if(res.data.code === 'success') {
                        this.isMarked = res.data.data.isFavorite
                    }
                })
            }
        }
    }
</script>

<style scoped lang="scss">
    .play-header {
        height: 86px;
        display: flex;
        background-color: #fff;
        justify-content: space-between;
        .title, .operation{
            margin: 23px;
        }
        .title {
            img{
                height: 40px;
                width: 40px;
                vertical-align: middle;
                margin-right: 10px;
            }
            span{
                vertical-align: middle;
                &.text-in-red{
                    color: #e70012;
                }
            }
        }
        .operation {
            display: flex;
            padding: 8px 0px;
            .mark{
                margin: 0px 10px;
            }
            img{
                width: 30px;
                height: 30px;
            }
        }

    }
    .play-content {
        width: 95%;
        margin: 14px auto;
        height: calc(100% - 114px);
        .player-wrapper,.info-note {
            display: inline-block;
            vertical-align: top;
        }
        .player-wrapper {
            width: 68%;
            margin: 0px 1%;
        }
        .info-note{
            width: 30%;
            .el-tabs {
                height: 100%;
                overflow-y: auto;
            }
            .add-note {
                margin: 15px;
                display: flex;
                justify-content: flex-end;
            }
        }
        .note-timeline{
            overflow: auto;
        }
    }
</style>