<template>
    <div class="page-wrapper">
        <div class="filter-wrapper">
            <div class="search-wrapper">
                <el-input placeholder="" v-model="searchKey" class="input-with-select">
                    <i slot="prepend" class="el-icon-search"></i>
                    <el-button slot="append" @click="getVideoList"></el-button>
                </el-input>
            </div>
        </div>
        <div class="table-wrapper" v-loading="loading" style="margin: 15px;">
            <el-table
                    ref="videoTable"
                    :data="videoListModel"
                    style="width: 100%"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        label=""
                        >
                    <template slot-scope="scope">
                        <div style="display: flex">
                            <div style="width: 90px;;height:60px; ">
                                <img :src="scope.row.coverUrl" style="height: 100%;width: 100%"/>
                            </div>
                            <div style="margin-left: 10px;">
                                <div style="color: #333;font-size: 16px;">
                                    <span>{{scope.row.title}}</span>
                                </div>
                                <div>
                                    <span>{{ transTime(scope.row.duration)}}</span>
                                </div>
                            </div>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        width="180">
                    <template slot-scope="scope">
                        <span>{{statusMap[scope.row.status]}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template  slot-scope="scope">
                        <el-button size="medium" @click="platformExamine(scope.row)"></el-button>
                        <el-button size="medium" @click="selfExamine(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-dialog
                title=""
                :visible.sync="addingExamineReport"
                width="30%"
                center>
            <div v-loading="loadingExamineInfo">
                <template v-if="examineInfo">
                    <div class="platform">
                        <div class="title">5</div>
                        <div class="items">
                            <div v-for="item in platExamineItems" class="item">
                                <div>
                                    <span>{{item.info}}</span>
                                </div>
                                <div class="record">
                                    <span>{{examineInfo[item.key]}}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="self">
                        <div class="title">4</div>
                        <div class="items">
                            <div v-for="item in selfExamineKeys" class="item">
                                <div>
                                    <span class="left-text">{{item.info}}</span>
                                </div>
                                <div>
                                    <el-radio v-model="examineInfo[item.key]" :label="0"></el-radio>
                                    <el-radio v-model="examineInfo[item.key]" :label="1"></el-radio>
                                </div>
                                <div class="record">
                                    <span>{{examineInfo[item.key]}}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </template>

            </div>
            <span slot="footer" class="dialog-footer">
            <el-button @click="addingExamineReport = false"> </el-button>
            <el-button type="primary" @click="submitExamineReport" :loading="submittingExamineReport"></el-button>
          </span>
        </el-dialog>
    </div>
</template>

<script>
    import untils from '../../../untils/index'
    import {VIDEO_STATUS} from '../../../untils/index'
    export default {
        name: "home",
        data() {
            return {
                searchKey: '',
                videoListModel: [
                ],
                loading:false,
                heatColors:["#F64A4A","#F64A4A","#F64A4A"],
                statusMap:VIDEO_STATUS,
                timeoutHandler:null,
                addingExamineReport:false,
                platExamineItems:[
                    {
                        key:"platAd",
                        info:''
                    },
                    {
                        key:"platLive",
                        info:""
                    },
                    {
                        key: "platLogo",
                        info:"LOGO"
                    },
                    {
                        key:"platPorn",
                        info:""
                    },
                    {
                        key:"platTerrorism",
                        info:""
                    }
                ],
                selfExamineKeys:[
                    // {
                    //     key:"userName",
                    //     info:''
                    // },
                    {
                        key:"userBeginning",
                        info:""
                    },
                    {
                        key:"userTitle",
                        info:""
                    },
                    {
                        key: "userSubtitle",
                        info:""
                    },
                    {
                        key:"userSubtitleMatch",
                        info:""
                    }
                ],
                loadingExamineInfo:false,
                examineInfo:{
                    "id": 0,
                    "platAd": 0,
                    "platLive": 0,
                    "platLogo": 0,
                    "platPorn": 0,
                    "platTerrorism": 0,
                    "userBeginning": 0,
                    "userSubtitle": 0,
                    "userSubtitleMatch": 0,
                    "userTitle": 0,
                    "videoId": "",
                    "userName": "",
                },
                currentReportVideo:'',
                submittingExamineReport:false
            }

        },
        created() {
            this.refreshList()
        },
        methods: {
            getVideoList() {
                this.loading = true;
                let param = {
                    queryConds: [],
                    sortConds: [
                        {
                            sortFieldName: "heat",
                            sortRule: "ASC"
                        }
                    ],
                    videoKind:'pri'
                };
                param.queryConds.push({
                    key: "filterStr",
                    value: this.searchKey
                })
                this.$http.post('/videoinfo/filters',param).then(res => {
                    if(res.data.code === 'success') {
                        this.videoListModel = res.data.data.videoInfos;
                        this.loading = false;
                    }else {
                        this.$notify({
                            title:'',
                            type:'error',
                            message:res.data.msg
                        });
                        this.loading = false
                    }
                }).catch((err)=>{
                    this.loading = false
                    this.$notify({
                        title:'',
                        type:'error',
                        message:err.message
                    });
                })
            },
            syncAliyun() {
                this.$http.post('/videoinfo/syncFromAliyun')
            },
            transTime(duration) {
                return  untils.formatTime(duration)
            },
            handleSelectionChange(selection){

            },
            refreshList() {
                this.getVideoList();
                this.timeoutHandler = setTimeout(()=>{
                    this.refreshList()
                },60000)
            },
            platformExamine(row) {
                this.$http.post('/videoinfo/checks',row)
            },
            getExamineInfo(videoId) {
                this.loadingExamineInfo = true;
                let url = `/reviewreports?videoId=${videoId}`
                this.$http.get(url).then(res => {
                    if(res.data.code === 'success') {
                        // res.data.data[0];
                        let data = {
                            "id": 0,
                            "platAd": 1,
                            "platLive": 1,
                            "platLogo": 0,
                            "platPorn": 1,
                            "platTerrorism": 0,
                            "userBeginning": 0,
                            "userName": "string",
                            "userSubtitle": 0,
                            "userSubtitleMatch": 0,
                            "userTitle": 0,
                            "videoId": ""
                        }
                        if(!data) {
                            this.$notify({
                                title:'',
                                type:'warning',
                                message:''
                            });
                            this.addingExamineReport = false;
                        }else {
                            Object.keys(data).forEach(key=>{
                                this.examineInfo[key] = data[key]
                            })
                        }
                        this.loadingExamineInfo = false;
                    }else {
                        this.loadingExamineInfo = false;
                        this.addingExamineReport = false;
                        this.$notify({
                            title:'',
                            type:'error',
                            message:res.data.msg
                        });
                    }
                }).catch(err => {
                    this.loadingExamineInfo = false;
                    this.addingExamineReport = false;
                    this.$notify({
                        title:'',
                        type:'error',
                        message:err.message
                    });
                })
            },
            selfExamine(video) {
                this.currentReportVideo = video;
                this.addingExamineReport = true;
                this.getExamineInfo(video.videoId)
            },
            submitExamineReport() {
                this.submittingExamineReport = true;
                this.examineInfo.id = this.currentReportVideo.id;
                this.examineInfo.videoId = this.currentReportVideo.videoId;
                this.examineInfo.userName = localStorage.getItem('user');
                this.$http.post('/reviewreports',this.examineInfo).then(res => {
                        if(res.data.code === 'success') {
                            this.submittingExamineReport = false;
                            this.addingExamineReport = false;
                            this.$notify({
                                title:'',
                                type:'success',
                            });
                        }
                })
            }
        },
        destroyed() {
            if(this.timeoutHandler) {
                clearTimeout(this.timeoutHandler)
            }
        }
    }
</script>

<style scoped>
    .filter-wrapper {
        height: 100px;
        background-color: #fff;
        padding: 30px 0px;
        box-sizing: border-box;
    }
    .search-wrapper {
        height: 48px;
        width: 350px;
        margin: 0px 20px;
    }
    .video-list-wrapper {
        display: flex;
        flex-wrap: wrap;
    }
    .video-list {
        margin: 30px;
    }
    .cover {
        width: 378px;
        height: 220px;
    }
    .cover  img {
        height: 100%;
        width: 100%;
    }
    .info {
        display: flex;
        justify-content: space-between;

    }
    .info .duration i{
        margin-right: 5px;
    }
    .item {
        display: flex;
        justify-content: space-between;
        margin: 10px;
    }
    .title{
        border-left: 6px solid #e70012;
        padding-left: 10px;
        font-size: 18px;
        color: #333;
        margin: 15px 0px;
    }
    .left-text{
        display: inline-block;
        width: 30%;
        width: 130px;
    }
</style>