<template>
    <div>

        <div class="edit-header">
            <div class="title"><span></span></div>
            <div class="operation">
                <el-button></el-button>
                <el-button type="warning" ></el-button>
                <el-button type="primary" @click="createVideo" :disabled="!showCursor"></el-button>
            </div>
        </div>
        <div class="video-add">
            <div class="video-add-left">
                <div id="play-dom" ref="playerDom"></div>
            </div>
            <div class="video-add-right">
                <div class="add-wrapper">
                    <el-tabs v-model="activeLabel" @tab-click="handleTabClick">
                        <el-tab-pane  name="source" v-if="!showCursor">
                            <span slot="label"><i class="el-icon-film"></i></span>
                            <div class="video-list" >
                                <div class="add-btn" @click="toggleDialog">
                                    
                                </div>
                                <!--:style="{background:'url('+item.CoverURL+')'+' no-repeat 100%'}"-->
                                <div class="added-list" v-for="item in checkedVideo" draggable="true" @dragstart="addToEidt($event,item)" >
                                    <img :src="item.coverUrl"/>
                                </div>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane  name="edit" v-else>
                            <span slot="label"><i class="el-icon-magic-stick"></i></span>
                            <div>
                                <el-checkbox v-model="showTextInVideo" ></el-checkbox>
                                <el-checkbox v-model="showImgInVideo" ></el-checkbox>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </div>
        </div>

        <!--@mousemove="mouseMove" @mouseup="mouseUp"-->
        <div class="video-edit" >
            <div class="edit-logo">
                <img src="../../../assets/image/edit-logo.png"/>
            </div>
            <div class="timeline-edit-container" ref="edit" @dragover="dragging" @click="showReactBox">
                <!--<div class="myDialog" :style="{left: currentLeftMark + 'px'}" v-if="showCutPanel">-->
                    <!--<div class="box-card">-->
                        <!--<i class="el-icon-check"/>-->
                    <!--</div>-->
                <!--</div>-->
                <div class="top-wrapper"  v-if="showCursor" :style="{left: currentLeftMark + 'px',width:(currentRightMark- currentLeftMark)+'px'}"></div>
                <div class="bottom-wrapper" v-if="showCursor" :style="{left: currentLeftMark + 'px',width:(currentRightMark- currentLeftMark)+'px'}"></div>
                <div class="left-wrapper"  :style="{left: currentLeftMark + 'px'}" v-if="showCursor" @click="doCut"></div>
                <div class="right-wrapper"  :style="{left: currentRightMark + 'px'}" v-if="showCursor" @click="doCut"></div>
                <!---->
                <template v-if="showCut">
                    <div class="mark" v-for="item in marks" :style="{left: item + 'px'}" :key="item"><i class="el-icon-scissors"/></div>
                </template>
                <!---->
                <template v-if="showCursor">
                    <div class="playingPoint" :style="{left: playingPoint + 'px'}" ></div>
                </template>
                <div class="play-line-container" draggable="true"  @dragstart="dragStart"  @dragend="dragEnd" @click="stopClickPropagation" :style="playLinePosition" v-if="showCut">
                    <!-- @mouseup="mouseUptwo"-->
                    <div class="play-line-cut" @click="mark">
                        <div class="play-line-cut-img">

                        </div>
                    </div>
                    <div class="play-line"></div>
                    <div class="play-line-time">{{currentPoint}}</div>
                </div>
                <div class="time-ruler" :style="{width: videoEditingList.length*1000+'px'}">
                    <div class="startLabel" v-if="videoEditingList.length > 0">
                        <div class="stick start"></div>
                        <div class="rulabel start">00:00:00</div>
                    </div>

                    <div class="section" v-for="(video, sectionIndex) in videoEditingList" :key="video.videoId" >
                        <template v-for="labelIndex in points" >
                            <div :style="{left:(labelIndex-1 + sectionIndex*8)*125+'px'}" class="video-snapshot">
                                <img :src="video.coverUrl"/>
                            </div>
                            <div class="stick" :style="{left:(labelIndex + sectionIndex*8)*125+'px'}"></div>
                            <div class="rulabel" :style="{left:((labelIndex + sectionIndex*8)*125 - 32)+'px'}">{{caculateLabel(sectionIndex,labelIndex)}}</div>
                        </template>
                    </div>
                </div>
            </div>
            <div class="edit-add" @dragover="allowDrop" @drop="drop">+</div>
        </div>
        <el-dialog
                class="custom-dialog"
                title=""
                :visible.sync="showAddDialog"
                width="50%"
                >
            <div>
                <div class="filter-wrapper">
                    <div class="search-wrapper">
                        <el-input placeholder="" v-model="searchKey" class="input-with-select">
                            <i slot="prepend" class="el-icon-search"></i>
                            <el-button slot="append" @click="getVideoList"></el-button>
                        </el-input>
                    </div>
                </div>
                <div class="video-list-wrapper">
                    <div v-for="item in videoList" @click="checkItem(item)" :class="item.checked ? 'checked' : ''" :key="item.videoId">
                        <img class="video-cover" :src="item.coverUrl"/>
                    </div>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="showAddDialog = false"> </el-button>
            </span>
        </el-dialog>
        <el-dialog
                class="creating-video"
                :visible.sync="creatingVideo"
                title=""
                width="30%">
            <div>
                <el-form :model="creatingVideoForm" :rules="creatingVideoFormRules" ref="videoForm" label-width="100px">
                    <el-form-item label="" prop="Title">
                        <el-input v-model="creatingVideoForm.Title"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="Description">
                        <el-input type="textarea" v-model="creatingVideoForm.Description"></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                 <el-button type="primary" @click="creatingVideo = false" v-if="!creating"></el-button>
                <el-button type="primary" @click="submitCreatingForm" :loading="creating"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import untils from '../../../untils'
    import {VIDEO_LIST} from '../../../untils'
    export default {
        name: "video-editor",
        data() {
            return {
                loading:false,
                searchKey:'',
                showAddDialog:false,
                playLinePosition:{
                    left: '0px'
                },
                down:false,
                startPointX:0,
                baseOffset:0,
                videoList:[],
                videoEditingList:[],
                marks:[],
                totalDuration:0,
                points: [1,2,3,4,5,6,7,8],
                currentPoint:'00:00:00',
                currentLeftMark:0,
                currentRightMark:0,
                showCursor:false,
                showCut:false,
                player:null,
                videoStartIndex:0,
                videoEndIndex:0,
                currentPlayingPoint:0,
                videoIn:0,
                videoOut:0,
                playingPoint:0,
                currentPlayingIndex:0,
                showTextInVideo:false,
                showImgInVideo:false,
                creatingVideo:false,
                creatingVideoForm: {
                    Title:'',
                    Description:''
                },
                creatingVideoFormRules:{
                    Title:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    Description:[
                        { required: true, message: '', trigger: 'blur' },
                        { min: 5, max: 20, message: ' 5  20 ', trigger: 'blur' }
                    ]
                },
                creating:false

            }
        },
        created() {
          this.getVideoList()
        },
        methods: {
            stopClickPropagation(event) {
                event.stopPropagation()
            },
            handleTabClick() {
                // this.activeLabel='source'
            },
            toggleDialog() {
                this.showAddDialog = !this.showAddDialog
            },
            getVideoList() {
                this.loading = true;
                let param = {
                    queryConds: [],
                    sortConds: [
                        {
                            sortFieldName: "heat",
                            sortRule: "ASC"
                        }
                    ],
                    videoKind:'material'
                };
                param.queryConds.push({
                    key: "filterStr",
                    value: this.searchKey
                })
                this.$http.post('/videoinfo/filters',param).then(res => {
                    if(res.data.code === 'success') {
                        this.videoList =[];
                         res.data.data.videoInfos.forEach((video) => {
                             if(!video.hasOwnProperty('checked')) {
                                 video.checked = false;
                             }
                             this.videoList.push(video)
                         });
                        this.loading = false;
                    }else {
                        this.$notify({
                            title:'',
                            type:'error',
                            message:res.data.msg
                        });
                        this.loading = false
                    }
                }).catch((err)=>{
                    this.loading = false
                    this.$notify({
                        title:'',
                        type:'error',
                        message:err.message
                    });
                })
            },
            mark(event) {
                let hasMark = this.marks.some( item => {
                    return item === this.baseOffset
                });
                if(!hasMark) {
                    this.marks.push(this.baseOffset)
                }
                console.log(this.marks)
            },
            dragStart(event){
                // 
                let img = new Image();
                // 
                event.dropEffect = "move";
                event.dataTransfer.setDragImage(img,0,0)
                this.startPointX = event.x;
            },
            dragging(event) {
                this.allowDrop(event);
                //
                let x = event.x - this.startPointX;
                //
                let offset = this.baseOffset + x
                let containerWidth = this.$refs.edit.getClientRects()[0].width
                if(offset < 0) {
                    offset = 0
                }
                if(offset > containerWidth) {
                    offset = containerWidth
                }
                // 
                if(containerWidth > 0) {
                    let value = this.totalDuration * (offset / containerWidth)
                    this.currentPoint = untils.formatTime(value)

                }
                this.playLinePosition.left = offset+'px';
                this.baseOffset = parseInt(this.playLinePosition.left)
                this.startPointX = event.x;
            },
            dragEnd(event) {
                //
                // let x = event.screenX - this.startPointX;
                //
                // let offset = this.baseOffset + x
                // if(offset < 0) {
                //     offset = 0
                // }
                // if(offset > this.$refs.edit.getClientRects()[0].width) {
                //     offset = this.$refs.edit.getClientRects()[0].width
                // }
                // this.playLinePosition.left = offset+'px';
                // this.baseOffset = parseInt(this.playLinePosition.left)
            },
            checkItem(item) {
                if(item.checked) {
                    item.checked = false
                } else {
                    item.checked = true
                }
            },
            addToEidt(event,item) {
                event.dataTransfer.dropEffect ='move'
                event.dataTransfer.setData('Text',item.videoId)
            },
            allowDrop(event) {
                event.preventDefault();
            },
            drop(event) {
                let videoId = event.dataTransfer.getData('Text')
                this.videoList.forEach(video => {
                    if(video.videoId == videoId) {
                        this.videoEditingList.push(video)
                        this.totalDuration+=video.duration;
                    }
                })
                this.showCut = true
            },
            caculateLabel(sectionIndex,labelIndex) {
                let timePoint = this.perDuration*(labelIndex + 8*sectionIndex)
                return untils.formatTime(timePoint)
            },
            showReactBox(event) {
                let baseX = this.$refs.edit.getBoundingClientRect().x;
                let baseY = this.$refs.edit.getBoundingClientRect().y;
                if( event.y -baseY <= 90) {
                    this.showCursor = true;
                    this.showCut = false;
                    let mouseOffset =  event.x -baseX;
                    this.findNearMark(mouseOffset)
                    this.initPlayer()
                }else {
                    this.showCursor = false;
                    this.showCut = true;
                }
            },
            findNearMark(mouseOffset) {
                let leftMark,rightMark;
                let leftMinDistance = undefined;
                let rightMinDistance = undefined;
                let delt;
                this.marks.forEach((mark) => {
                     // 
                     if(mark < mouseOffset) {
                         delt = mouseOffset - mark;
                         if(leftMinDistance && delt < leftMinDistance) {
                             leftMinDistance = delt;
                             leftMark = mark;
                         }
                         if(!leftMinDistance) {
                             leftMinDistance = delt;
                             leftMark = mark;
                         }
                     // 
                     }else {
                         delt = mark - mouseOffset;
                         if(rightMinDistance && delt < rightMinDistance) {
                             rightMinDistance = delt;
                             rightMark = mark
                         }
                         if(!rightMinDistance) {
                             rightMinDistance = delt;
                             rightMark = mark;
                         }
                     }
                })
                // 
                if(!leftMark) {
                    leftMark =0
                }
                //
                if(!rightMark) {
                    rightMark = this.videoEditingList.length * 1000;
                }
                this.currentLeftMark = leftMark;
                this.currentRightMark = rightMark;
                console.log('left:' + leftMark)
                console.log('right:' + rightMark)
                console.log(this.marks)
            },
            initPlayer() {
                this.videoStartIndex = parseInt(this.currentLeftMark / 1000) ;
                this.videoEndIndex = parseInt(this.currentRightMark / 1000) ;
                if((this.currentRightMark % 1000) === 0 ) this.videoEndIndex= this.videoEndIndex-1; //
                let inDelt = this.currentLeftMark % 1000;
                let outDelt =this.currentRightMark % 1000;
                let totalDistance = 1000 * this.videoEditingList.length
                this.videoIn = (inDelt / totalDistance)*this.totalDuration;
                this.videoOut = (outDelt/ totalDistance)*this.totalDuration;
                this.currentPlayingIndex = this.videoStartIndex;
                let playPros = {
                    id:'play-dom',
                    width:'100%',
                    height:'100%',
                    autoplay:true,
                    rePlay:false,
                    source:this.videoEditingList[this.currentPlayingIndex].url,
                    cover:this.videoEditingList[this.currentPlayingIndex].coverUrl
                }
                if(this.player) {
                    this.player.loadByUrl(this.videoEditingList[this.currentPlayingIndex].url)
                }else {
                    this.player = new  Aliplayer(playPros)
                    this.player.on('timeupdate',(value) => {
                        this.playingPoint = this.player.getCurrentTime()*(totalDistance/this.totalDuration)+1000*this.currentPlayingIndex;
                        if(this.currentPlayingIndex ===this.videoEndIndex && this.player.getCurrentTime() > this.videoOut) {
                            this.player.pause()
                        }
                    })
                    this.player.on('ready',(value) => {
                        if(this.currentPlayingIndex === this.videoStartIndex) {
                            this.player.seek(this.videoIn)
                        }
                    })
                    this.player.on('ended',(value) => {
                        if(this.currentPlayingIndex < this.videoEndIndex) {
                            this.currentPlayingIndex ++;
                            this.player.loadByUrl(this.videoEditingList[this.currentPlayingIndex].url)
                        }
                    })

                }
            },
            doCut(event) {
                this.stopClickPropagation(event)
                this.toggleCutPanel()

            },
            toggleCutPanel() {
                this.showCutPanel = !this.showCutPanel
            },
            createVideo() {
                this.creatingVideo = true;
            },
            submitCreatingForm() {
                this.$refs.videoForm.validate((valid)=>{
                    if(valid){
                        this.creating = true;
                        let params = {
                            mediaMetadata :{
                                mediaMetadata : this.creatingVideoForm
                            }
                        };
                        let timeLine={
                            VideoTracks:[
                                {
                                    VideoTrackClips: []
                                }
                            ]
                        };
                        for(let i=this.videoStartIndex;i<=this.videoEndIndex;i++) {
                            let value={};
                            value.MediaId = this.videoEditingList[i].videoId;
                            if(i === this.videoStartIndex) {
                                value.In = this.videoIn
                            }
                            // 
                            if(i === this.videoEndIndex && this.videoOut!== 0) {
                                value.Out = this.videoOut
                            }
                            timeLine.VideoTracks[0].VideoTrackClips.push(value)
                        }
                        params.timeline = timeLine;
                        params.produceConfig = {
                            TemplateGroupId:null
                        }
                        this.$http.post('/videoedits',params).then(res => {
                            if(res.data.code === "success") {
                                this.creatingVideo = false;
                                this.creating = false;
                                this.$router.push("/main/videoInfo")

                            }else {
                                this.$notify({
                                    title:'',
                                    type:'error',
                                    message:res.data.msg
                                })
                                this.creatingVideo = false;
                                this.creating = false;
                            }
                        }).catch((err)=>{
                            this.creatingVideo = false;
                            this.creating = false;
                            this.$notify({
                                title:'',
                                type:'error',
                                message:err.message
                            })
                        })
                    }
                })
            }
        },
        computed: {
            checkedVideo() {
                return this.videoList.filter((item)=>{
                    return item.checked
                })
            },
            perDuration() {
                if(this.videoEditingList.length > 0){
                    return this.totalDuration / ( this.videoEditingList.length * 8)
                }else {
                    return 0;
                }

            },
            activeLabel: {
                get() {
                    return this.showCursor ? 'edit':'source'
                },
                set() {

                }

            }

        }
    }
</script>

<style scoped lang="scss">
    .edit-header {
        height: 86px;
        display: flex;
        background-color: #fff;
        justify-content: space-between;
        .title, .operation{
            margin: 23px;
        }
    }
    .video-add {
        margin: 15px;
        height: 360px;
        background-color: #fff;
        .video-add-left {
            position: relative;
            width: 50%;
            height: 100%;
            display: inline-block;
            vertical-align: top;
            background-color: #333333;
        }
        .video-add-right {
            width: 50%;
            height: 100%;
            box-sizing: border-box;
            padding: 10px;
            display: inline-block;
            vertical-align: top;
        }
    }
    .video-list{
        display: flex;
        flex-wrap: wrap;
        .add-btn{
            width: 120px;
            height: 68px;
            margin: 10px;
            border: 1px dashed #c3c5c6;
            text-align: center;
            line-height: 68px;
            color: #F64A4A;
            cursor: pointer;
        }
        .add-btn:hover {
            border-color: #F64A4A;
            color: #F64A4A;
        }
        .added-list{
            width: 120px;
            height: 68px;
            margin: 10px;
            border: 1px dashed #c3c5c6;
            img {
                height: 100%;
                width: 100%;
            }
        }
    }
    .video-edit {
        padding: 20px 0px;
        margin: 15px;
        position: relative;
        display: flex;
        background: #fcfcfc;
        border: 1px solid #e6e6e6;
        width: auto;
        top: 5px;
        overflow: auto;
    }
    .edit-logo{
        height: 116px;
        width: 70px;
        margin: 0px 15px;
        text-align: center;
        line-height: 116px;
        flex-shrink: 0;
    }
    .timeline-edit-container {

        position: relative;
        height: 178px;
        direction: ltr;
        .play-line-container {
            position: absolute;
            width: 0;
            top: 0;
            bottom: 0;
            z-index: 4;
            cursor: col-resize;
        }
        .play-line-cut {
            position: absolute;
            width: 24px;
            height: 16px;
            top: 43px;
            left: -11px;
            z-index: 8;
            cursor: pointer;
        }
        .play-line-cut-img {
            position: relative;
            width: 40px;
            height: 25px;
            background-size: 100%;
            background: url(data:image/png;base64,) no-repeat;
            left: -6px;
        }
        .play-line {
            position: absolute;
            background: #e70012;
            width: 2px;
            z-index: 4;
            height: 116px;
        }
        .play-line-time {
            position: absolute;
            height: 16px;
            z-index: 8;
            background: #e70012;
            width: 64px;
            color: #fff;
            text-align: center;
            vertical-align: middle;
            top:116px;
            left:-32px;
        }
    }
    .edit-add {
        width: 120px;
        height: 68px;
        border: 1px dashed #c3c5c6;
        box-sizing: border-box;
        text-align: center;
        line-height: 68px;
        color: #F64A4A;
        cursor: pointer;
        flex-shrink:0;
        margin-top: 20px;
        &:hover {
            border-color: #F64A4A;
            color: #F64A4A;
        }
    }
    .video-list-wrapper {
        display: flex;
        flex-wrap: wrap;
        div {
            box-sizing: border-box;
            background-color: yellow;
            width: 120px;
            margin: 10px;
            height: 68px;
            &.checked {
                border: 2px solid #e70012;
            }
        }
        .video-cover {
            height: 100%;
            width: 100%;
        }
    }
    .section{
        position: absolute;
        border-top: 1px solid #333333;
        top: 100px;
        width: 100%;
    }
    .stick{
        position: absolute;
        width: 1px;
        height: 9px;
        background-color: #333;
        top:-6px;
    }
    .rulabel{
        position: absolute;
    }
    .start{
        top:100px;
    }
    .stick.start{
        height: 10px;
        top:95px;;
    }
    .mark {
        position: absolute;
        width: 1px;
        height: 90px;
        border-left: 1px dotted #F64A4A;
        z-index: 3;
        color: #F64A4A;
        i {
            margin-left: -9px;
            line-height: 100px;
        }
    }
    .playingPoint{
        position: absolute;
        width: 1px;
        height: 90px;
        border-left: 2px solid #F64A4A;
        z-index: 3;
        color: #F64A4A;
        i {
            margin-left: -9px;
            line-height: 100px;
        }
    }
    .left-wrapper,.right-wrapper {
        position: absolute;
        height: 90px;
        width: 13px;
        z-index: 9;
        background: url("data:image/png;base64,") no-repeat;
    }
    .top-wrapper, .bottom-wrapper {
        position: absolute;
        height: 1px;
        background-color: #F64A4A;
    }
    .bottom-wrapper {
        top:90px;
    }
   .video-snapshot {
       position: absolute;
       width: 124px;
       top:-100px;
       height: 90px;
       img {

           height: 100%;
           width: 100%;
       }
   }
    .filter-wrapper {
        height: 100px;
        background-color: #fff;
        padding: 30px 0px;
        box-sizing: border-box;
    }
    .search-wrapper {
        height: 48px;
        width: 350px;
        margin: 0px 20px;
    }
</style>