<template>
    <div class="page-wrapper">
        <div class="title-wrapper" style="padding: 50px 20px 0px 20px">
            <div class="title"></div>
        </div>
        <div class="body-wrapper">
            <el-tabs v-model="activeLib" tab-position="left" @tab-click="handleClick" class="video-lib-tabs">
                <el-tab-pane label="" name="video">
                    <el-tabs v-model="activeVideoType" type="border-card" @tab-click="handleClick">
                        <el-tab-pane label="" name="list"></el-tab-pane>
                        <el-tab-pane label="" name="upload"></el-tab-pane>
                        <template v-if="activeVideoType === 'upload'">
                            <el-button class="upload-video" type="primary" icon="el-icon-circle-plus-outline" @click="toggleUploadDialog">

                            </el-button>
                            <div class="uploading-table">
                                <el-table
                                        :data="uploadings"
                                        border
                                        style="width: 100%">
                                    <el-table-column
                                            prop="name"
                                            label=""
                                            width="180">
                                    </el-table-column>
                                    <el-table-column
                                            prop="type"
                                            label=""
                                            width="180">
                                    </el-table-column>
                                    <el-table-column
                                            prop="size"
                                            label="">
                                        <template slot-scope="scope">
                                            <span>{{parseInt((scope.row.size/1000000))}}MB</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column

                                            label="">
                                        <template slot-scope="scope">
                                           <span>{{scope.row.cateId}}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="">
                                        <template slot-scope="scope">
                                            <el-progress :percentage="scope.row.percentage"></el-progress>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </div>
                        </template>
                        <template v-if="activeVideoType === 'list'">
                            <div class="video-list-wrapper">
                                <div class="video-list" v-for="video in videoListModel" @click="playVideo(video)">
                                    <el-card class="video-card" >
                                        <div class="cover">
                                            <img :src="video.coverUrl"/>
                                        </div>
                                        <div class="title">
                                            <span alt="asdsf">{{video.title}}</span>
                                        </div>
                                        <div class="abstract">
                                            <span>{{video.abstract}}</span>
                                        </div>
                                        <el-divider></el-divider>
                                        <div class="info">
                                            <div class="duration">
                                                <!--{{formateTime(video.duration)}}-->
                                                <i class="el-icon-time"/>
                                                <span >{{transTime(video)}}</span>
                                            </div>
                                            <div class="heat">
                                                <el-rate
                                                        v-model="video.heat"
                                                        disabled
                                                        show-score
                                                        :colors=heatColors
                                                >
                                                </el-rate>
                                            </div>
                                        </div>
                                    </el-card>
                                </div>
                            </div>
                        </template>
                    </el-tabs>
                </el-tab-pane>
                <el-tab-pane label="" name="picture"></el-tab-pane>
                <el-tab-pane label="" name="marked"></el-tab-pane>
            </el-tabs>
            <el-dialog class="upload-choice"
                       title=""
                       :visible.sync="showUploadDialog"
                       >
                <div>
                    <div>
                        <el-button type="primary" @click="clickInput">
                            <input class="upload-video" ref="videoUploader" type="file" name="file" id="files" multiple @change="filesChange"/>
                        </el-button>
                    </div>
                    <el-table
                            :data="toUploadList"
                            height="250"
                            border
                            style="width: 100%">
                        <el-table-column
                                prop="name"
                                label=""
                                width="180">
                        </el-table-column>
                        <el-table-column
                                prop="type"
                                label=""
                                width="180">
                        </el-table-column>
                        <el-table-column
                                prop="size"
                                label="">
                            <template slot-scope="scope">
                               <span>{{parseInt((scope.row.size/1000000))}}MB</span>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="cate"
                                label="">
                            <template slot-scope="scope">
                                <el-select v-model="scope.row.cateId"  placeholder=""  @change="changType($event,scope.row,scope.$index)">
                                    <el-option
                                            v-for="item in videoTypes"
                                            :key="item.cateId"
                                            :label="item.cateName"
                                            :value="item.cateId"
                                    >
                                    </el-option>
                                </el-select>
                            </template>
                        </el-table-column>
                        <el-table-column label="">
                            <template slot-scope="scope">
                                <el-button
                                        size="mini"
                                        type="danger"
                                        @click="handleDelete(scope.$index, scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div slot="footer" >
                    <el-button @click="toggleUploadDialog"> </el-button>
                    <el-button type="primary" @click="addUpload"></el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
    import untils from '../../../untils/index'
    import ElDialog from "element-ui/packages/dialog/src/component";
    import ElButton from "element-ui/packages/button/src/button";
    export default {
        components: {
            ElButton,
            ElDialog},
        name: "home",
        data() {
            return {
                userId:'1952136480414555',
                region:'cn-shanghai',
                searchKey: '',
                videoListModel: [
                ],
                loading:false,
                videoTypes:[],
                heatColors:["#F64A4A","#F64A4A","#F64A4A"],
                activeLib:'video',
                activeVideoType:'list',
                toUploadList:[],
                uploadings:[],
                showUploadDialog:false
            }
        },
        created() {
            // this.getMarkedList()
            this.getVideoTypes();
            this.getVideoList();
        },
        methods: {
            playVideo(video) {
                this.$store.commit('setVideoInfo',video);
                this.$router.push({
                    path:'/main/videoPlaying',
                    query:{
                        id:video.id
                    }
                })
            },
            getVideoTypes() {
                this.$http.get('/videoTypes').then(res=>{
                    if(res.data.code ==='success') {
                        this.videoTypes = res.data.data;
                    }
                })
            },
            getVideoList() {
                this.loading = true;
                let param = {
                    queryConds: [],
                    sortConds: [
                        {
                            sortFieldName: "heat",
                            sortRule: "ASC"
                        }
                    ],
                    videoKind:'material'
                };
                param.queryConds.push({
                    key: "filterStr",
                    value: this.searchKey
                })
                this.$http.post('/videoinfo/filters',param).then(res => {
                    if(res.data.code === 'success') {
                        this.videoListModel = res.data.data.videoInfos;
                        this.loading = false;
                    }else {
                        this.$notify({
                            title:'',
                            type:'error',
                            message:res.data.msg
                        });
                        this.loading = false
                    }
                }).catch((err)=>{
                    this.loading = false
                    this.$notify({
                        title:'',
                        type:'error',
                        message:err.message
                    });
                })
            },
            transTime(video) {
                return  untils.formatTime(video.duration)
            },
            getMarkedList() {
                this.loading = true;
                this.$http.get('/favoriteInfos').then(res=> {
                    if(res.data.code === 'success') {
                       this.videoListModel =[]
                        this.loading = false;
                        res.data.data.forEach(item => {
                            let url='/videoinfo/'+ item.videoId;
                            this.$http.get(url).then(res=>{
                                if(res.data.code === 'success'){
                                    this.videoListModel.push(res.data.data)
                                }
                            })
                        })

                    }
                })
            },
            getPictureList() {
                this.loading = true;
                this.$http.get('/pictureInfos').then(res => {
                    this.videoListModel = res.data.data;
                    this.loading = false;
                })
            },
            handleClick(){
                if(this.activeName === 'video') {
                    // console.log('video')
                    this.getVideoList()
                }else if(this.activeName === 'marked') {
                    this.getMarkedList()
                }else {
                    this.getPictureList()
                }
            },
            clickInput() {
                this.$refs.videoUploader.dispatchEvent(new MouseEvent('click'));
            },
            getUploadInfo() {
                this.$http.get('/videoinfo/uploadtoken').then(res=>{
                    console.log(res);
                })
            },
            filesChange(e) {
                let files = e.target.files;
                for(let i=0;i<files.length;i++) {
                    files[i].cateId = this.videoTypes[0].cateId;
                    this.toUploadList.push(files[i])
                }
            },
            toggleUploadDialog() {
                this.showUploadDialog = !this.showUploadDialog;
                //
                if(this.showUploadDialog) {
                    this.toUploadList = []
                }
            },
            handleDelete(index,row) {
                this.toUploadList.splice(index,1)
            },
            changType(type,item,index){
                item.cateId = type;
                // 
                this.$set(this.toUploadList,index,item)
            },
            addUpload() {
                this.toggleUploadDialog()
                while(this.toUploadList.length >=1 ) {
                    let file = this.toUploadList.shift();
                    let item =Object.create(null);
                    item.name = file.name;
                    item.size = file.size;
                    item.type = file.type;
                    item.cateId = file.cateId;
                    item.percentage = 0;
                    this.createUploader(item);
                    this.uploadings.push(item);
                    item.uploader.addFile(file);
                    item.uploader.startUpload();
                }
            },
            createUploader(item){
                let that = this;
                item.uploader = new AliyunUpload.Vod( {
                    timeout: 60000,
                    partSize: 1048576,
                    parallel:  5,
                    retryCount:  3,
                    retryDuration:  2,
                    region: this.region,
                    userId: this.userId,
                    addFileSuccess: function (uploadInfo) {
                        console.log(''+uploadInfo.file.name)
                    },
                    onUploadstarted:function (uploadInfo) {
                        let param= {
                            action:'CreateUploadVideo',
                            cateId:item.cateId,
                            description:'',
                            fileName:item.name,
                            title:item.name
                        };
                        console.log('')
                        that.$http.post('/videoinfo/uploadtoken',param).then(res =>{
                            if(res.data.code === 'success') {
                                let data = res.data.data;
                                item.uploader.setUploadAuthAndAddress(uploadInfo,data.uploadAuth,data.uploadAddress,data.videoId);
                            }
                        })
                    },
                    onUploadSucceed:function (uploadInfo) {
                        item.isSuccess = true;
                        let index = that.uploadings.indexOf(item);
                        if(index !== -1) {
                            that.$set(that.uploadings,index,item)
                        }
                    },
                    onUploadProgress: function (uploadInfo, totalSize, progress) {
                        console.log("onUploadProgress:file:" + uploadInfo.file.name + ", fileSize:" + totalSize + ", percent:" + Math.ceil(progress * 100) + "%")
                        item.percentage = Math.ceil(progress * 100);
                        let index = that.uploadings.indexOf(item);
                        if(index !== -1) {
                            that.$set(that.uploadings,index,item)
                        }
                    },
                    onUploadFailed: function (uploadInfo, code, message) {
                        console.log("onUploadFailed: file:" + uploadInfo.file.name + ",code:" + code + ", message:" + message)
                    },
                    onUploadTokenExpired: function (uploadInfo) {
                        // ,  UploadAuth 
                        //  uploadInfo.videoId (https://help.aliyun.com/document_detail/55408.html) UploadAuth
                        //  resumeUploadWithAuth , ,  UploadAuth
                        console.log('...');
                    },
                    onUploadEnd: function (uploadInfo) {
                        console.log("onUploadEnd: uploaded all the files")
                    }

                })
            }

        },
        computed:{

        }
    }
</script>

<style scoped>
    .page-wrapper {
        height: 100%;
    }
    .title-wrapper {
        height: 100px;
        padding: 30px 0px;
        box-sizing: border-box;
        background-image: url("../../../assets/image/banner-header.png");
        color: #e70012;
    }
    .body-wrapper{
        background-color: #fff;
        margin: 15px;
        height: calc(100% - 130px);
    }
    .video-list-wrapper {
        display: flex;
        flex-wrap: wrap;
    }
    .video-list {
        margin: 30px;
    }
    .cover {
        width: 250px;
        height: 160px;
    }
    .cover  img {
        height: 100%;
        width: 100%;
    }
    .info {
        display: flex;
        justify-content: space-between;

    }
    .info .duration i{
        margin-right: 5px;
    }

    input.upload-video {
        display: none;
    }
    div.title {
        width: 250px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
</style>