export default {
    // xx:yy:zz 
    formatTime : function(seconds) {
        seconds = parseInt(seconds)
        let x,y,z;
        if(seconds < 60) {
            z = seconds < 10 ? '0'+seconds :seconds
            return '00:00:'+ z ;
        }else if(seconds>= 60 && seconds < 3600) {
            z = seconds % 60 ;
            y = (seconds - z) / 60;
            z = z < 10 ? '0'+z : z;
            y = y < 10 ? '0'+y : y;
            return '00:'+y+':'+z
        }else {
            let  temp = seconds % 3600;
            x = (seconds - temp) / 3600 ;
            z = temp % 60;
            y = (temp -z) /60;
            x = x < 10 ? '0'+x : x;
            y = y < 10 ? '0'+y : y;
            z = z < 10 ? '0'+z : z;
            return x+':'+ y + ':' + z
        }
    }
}
